@mixin border-radius($radius)
{
    border-radius: $radius;
    -moz-border-radius: $radius;
    -webkit-border-radius: $radius;
}
@mixin box-shadow($color)
{
    -webkit-box-shadow: 0px 0px 5px 0px $color;
    -moz-box-shadow: 0px 0px 5px 0px $color;
    box-shadow: 0px 0px 5px 0px $color;
}

@mixin gradient() {
    background: #001aac;
    background: -moz-linear-gradient(-45deg,  #001aac 0%, #7a1eba 88%);
    background: -webkit-linear-gradient(-45deg,  #001aac 0%,#7a1eba 88%);
    background: linear-gradient(135deg,  #001aac 0%,#7a1eba 88%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#001aac', endColorstr='#7a1eba',GradientType=1 );
}

/** Map Deep Get
*   @author Hugo Giraudel
*   @access public
*   @param {Map} $map - Map
*   @param {Arglist} $keys - Key chain
*   @return {*} - Desired value
*/
@function map-deep-get($map, $keys...) {
    @each $key in $keys {
        $map: map-get($map, $key);
    }
    @return $map;
}
@mixin breakpoint-quickmenu($point)
{
    @if $point == large {
        @media (min-width: 1270px) { @content; }
    }
    @else if $point == medium {
        @media (min-width: 1024px) { @content; }
    }
    @else if $point == small {
        @media (min-width: 768px) { @content; }
    }
    @else if $point == mobile-landscape { /* Quick Menu support for mobile devices in landscape. Includes all iPhones (tested upto iPhone 12 PRO Max) and all Androids (tested upto Galaxy S20 Ultra). */
        @media (max-height: 428px) and (orientation: landscape) { @content; }
    }
}
@mixin dark-mode
{
    @media (prefers-color-scheme: dark) { @content; }
}
